"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm, type SubmitHandler } from "react-hook-form";
import { z } from "zod";
import { useState, useTransition } from "react";
import Image from "next/image";

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Separator } from "@/components/ui/separator";
import { useToast } from "@/hooks/use-toast";
import { getAILayoutSuggestion } from "@/app/actions";
import type { SuggestLayoutOutput } from "@/ai/flows/suggest-layout";
import { FileText, Lightbulb, Palette, Sparkles, FileDown, Loader2, Download, Settings2, Info } from "lucide-react";

const formSchema = z.object({
  content: z.string().min(50, "Content must be at least 50 characters long."),
  contentLength: z.enum(["short", "medium", "long"], {
    required_error: "Please select content length.",
  }),
  brandingElements: z.string().min(10, "Branding description must be at least 10 characters long."),
});

type FormValues = z.infer<typeof formSchema>;

export function PrintForgeForm() {
  const { toast } = useToast();
  const [isPendingAI, startAITransition] = useTransition();
  const [isPendingPDF, startPDFTransition] = useTransition();
  const [aiSuggestion, setAISuggestion] = useState<SuggestLayoutOutput | null>(null);
  const [pdfGenerated, setPdfGenerated] = useState(false);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      content: "",
      contentLength: undefined,
      brandingElements: "",
    },
  });

  const handleSuggestLayout = async () => {
    const { contentLength, brandingElements } = form.getValues();
    if (!contentLength || !brandingElements) {
      toast({
        title: "Missing Information",
        description: "Please provide content length and branding elements to get AI suggestions.",
        variant: "destructive",
      });
      if (!contentLength) form.setError("contentLength", { message: "Required" });
      if (!brandingElements) form.setError("brandingElements", { message: "Required" });
      return;
    }
    
    // Clear previous errors if any for these specific fields
    form.clearErrors("contentLength");
    form.clearErrors("brandingElements");

    startAITransition(async () => {
      try {
        setAISuggestion(null);
        const suggestion = await getAILayoutSuggestion({ contentLength, brandingElements });
        setAISuggestion(suggestion);
        toast({
          title: "AI Suggestion Ready!",
          description: "Layout suggestion has been generated.",
        });
      } catch (error) {
        console.error(error);
        toast({
          title: "AI Suggestion Failed",
          description: "Could not get layout suggestion. Please try again.",
          variant: "destructive",
        });
      }
    });
  };

  const onSubmit: SubmitHandler<FormValues> = (data) => {
    startPDFTransition(() => {
      // Simulate PDF generation
      console.log("Form submitted for PDF generation:", data);
      setPdfGenerated(false); // Reset before "generating"
      setTimeout(() => {
        setPdfGenerated(true);
        toast({
          title: "PDF Generated!",
          description: "Your PDF is ready for download (simulated).",
        });
      }, 2000); // Simulate 2s generation time
    });
  };
  
  const handleDownloadPdf = () => {
    if (!pdfGenerated) {
      toast({
        title: "PDF Not Generated",
        description: "Please generate the PDF first.",
        variant: "destructive",
      });
      return;
    }
    console.log("Download PDF clicked (simulated)");
    toast({
      title: "Download Started (Simulated)",
      description: "Your PDF download would start now.",
    });
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-8 items-start">
      <Card className="md:col-span-2 shadow-xl">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-2xl">
            <Settings2 className="h-7 w-7 text-primary" />
            Configure Your Document
          </CardTitle>
          <CardDescription>
            Fill in the details below to create your custom PDF.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
              <FormField
                control={form.control}
                name="content"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-2 text-lg"><FileText className="h-5 w-5" />Document Content</FormLabel>
                    <FormControl>
                      <Textarea
                        placeholder="Enter the main content for your PDF here. Be descriptive and provide all necessary text..."
                        className="min-h-[200px] resize-y"
                        {...field}
                      />
                    </FormControl>
                    <FormDescription>
                      This is the primary text that will appear in your PDF.
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                <FormField
                  control={form.control}
                  name="contentLength"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="flex items-center gap-2 text-lg"><Info className="h-5 w-5" />Content Length</FormLabel>
                      <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="Select content length" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          <SelectItem value="short">Short (e.g., a flyer, brief memo)</SelectItem>
                          <SelectItem value="medium">Medium (e.g., a report, proposal)</SelectItem>
                          <SelectItem value="long">Long (e.g., a manual, detailed guide)</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormDescription>Helps AI suggest a suitable layout.</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="brandingElements"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="flex items-center gap-2 text-lg"><Palette className="h-5 w-5" />Branding Elements</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., 'Logo top-left, footer with contact info'" {...field} />
                      </FormControl>
                      <FormDescription>Describe your branding (logo, colors, fonts).</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>
              
              <Separator className="my-8" />

              <div className="flex flex-col sm:flex-row justify-start gap-4">
                <Button type="submit" size="lg" disabled={isPendingPDF || isPendingAI}>
                  {isPendingPDF ? (
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                  ) : (
                    <Sparkles className="mr-2 h-5 w-5" />
                  )}
                  Generate PDF
                </Button>
                <Button
                  type="button"
                  variant="outline"
                  size="lg"
                  onClick={handleDownloadPdf}
                  disabled={!pdfGenerated || isPendingPDF || isPendingAI}
                >
                  <FileDown className="mr-2 h-5 w-5" />
                  Download PDF
                </Button>
              </div>
              <FormDescription className="mt-2">
                Generate your PDF. Store options for Google Drive or internal storage will be available soon.
              </FormDescription>
            </form>
          </Form>
        </CardContent>
      </Card>

      <div className="space-y-6 md:col-span-1">
        <Card className="shadow-xl">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-xl">
              <Lightbulb className="h-6 w-6 text-primary" />
              AI Layout Suggestion
            </CardTitle>
            <CardDescription>Get an optimal layout based on your input.</CardDescription>
          </CardHeader>
          <CardContent>
            <Button
              type="button"
              onClick={handleSuggestLayout}
              className="w-full"
              disabled={isPendingAI || isPendingPDF}
            >
              {isPendingAI ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <Sparkles className="mr-2 h-4 w-4" />
              )}
              Get Suggestion
            </Button>
            {aiSuggestion && (
              <Alert className="mt-6 bg-primary/5 border-primary/20">
                <Sparkles className="h-5 w-5 text-primary" />
                <AlertTitle className="font-semibold text-primary">Layout Suggestion</AlertTitle>
                <AlertDescription className="mt-2 space-y-2 text-foreground/80">
                  <p><strong>Suggestion:</strong> {aiSuggestion.layoutSuggestion}</p>
                  <p><strong>Reasoning:</strong> {aiSuggestion.reasoning}</p>
                </AlertDescription>
              </Alert>
            )}
             {!aiSuggestion && !isPendingAI && (
                <div className="mt-6 p-4 text-center text-muted-foreground">
                  <Image src="https://placehold.co/300x200.png" alt="AI suggestion placeholder" width={300} height={200} className="mx-auto rounded-md mb-2" data-ai-hint="abstract idea" />
                  Click the button above to get AI-powered layout ideas.
                </div>
            )}
            {isPendingAI && (
                <div className="mt-6 p-4 text-center text-muted-foreground flex flex-col items-center">
                    <Loader2 className="h-8 w-8 animate-spin text-primary mb-2" />
                    <p>Generating AI suggestion...</p>
                </div>
            )}
          </CardContent>
        </Card>
         <Card className="shadow-md bg-accent/10 border-accent/20">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-xl text-accent">
              <Download className="h-6 w-6" />
              Delivery Options
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <Button variant="outline" className="w-full" disabled>
              <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="mr-2"><path d="M14.5 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V7.5L14.5 2z"></path><polyline points="14 2 14 8 20 8"></polyline><path d="m10.4 12.6-1.8 5.4 1.8.6 1.8-.6-1.8-5.4Z"></path><path d="M15 18h1.1"></path><path d="M12.5 15h2.1"></path><path d="M17.5 15h.1"></path><path d="m20.6 12.5-1.8-.6-1.8.6 1.8 5.4Z"></path></svg>
              Save to Google Drive (Soon)
            </Button>
            <Button variant="outline" className="w-full" disabled>
              <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="mr-2"><path d="M12 20h.01"></path><path d="M8.5 16.4A2.5 2.5 0 0 0 6 18.5V20h12v-1.5a2.5 2.5 0 0 0-2.5-2.5h-7Z"></path><path d="M5 10V5c0-1.7 1.3-3 3-3h8c1.7 0 3 1.3 3 3v5"></path></svg>
              Save to Internal Storage (Soon)
            </Button>
            <p className="text-xs text-muted-foreground text-center pt-2">More ways to get your PDF are coming!</p>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
